<template>
    <div>
        <div class="box-title">
            <h5>可使用的计算模型</h5>
        </div>      
        
        <!-- 数据内容 -->
        <div class="box-container">
            <el-table
                v-loading="loading"
                :data="computeList"
                style="width: 100%">
                <el-table-column align="center" label="序号" prop="id"></el-table-column>
                <el-table-column align="center" label="模型名称" prop="name"></el-table-column>
                <el-table-column align="center" label="模型描述" prop="description"></el-table-column>
                <!-- <el-table-column align="center" label="计算地址" prop="url"></el-table-column> -->
                <el-table-column align="center" label="模型使用状态">
                    <template slot-scope="{row}">
                        <el-tag :type="row.status | statusType">{{ row.status | statusVal }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="操作">
                    <template slot-scope="scope">
                        <el-button type="success" size="small" @click="handleUse(scope.row)">使用</el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- <el-table
                v-loading="loading"
                :data="computeList"
                style="width: 100%">
                <el-table-column align="center" label="操作">
                    <el-table-column align="center" label="操作">
                        <template>
                            <el-button type="success" size="small" @click="getKey">获取密钥</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
                
                <el-table-column align="center" label="操作">
                    <template>
                        <el-button type="success" size="small">获取数据B</el-button>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="数据a">
                    <template>
                        <el-button type="primary" size="small" @click="getDataA">申请使用数据a</el-button>
                        <el-button type="primary" size="small" @click="getDataA">待审核</el-button>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="数据b">
                    <template>
                        <el-button type="success" size="small" @click="getDataB">申请使用数据b</el-button>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="操作">
                    <template>
                        <el-button type="success" size="small" @click="getCompute">计算</el-button>
                    </template>
                </el-table-column>
            </el-table> -->
        </div>
        
        <!-- 底部导航页 -->
        <div class="bottom-page">
            <Pagination
                :totalMsg="query.total"
                :pageSize="query.pageSize"
                :pageIndex="query.pageIndex"
                @setSize="getSize"
                @setIndex="getIndex"
            >
            </Pagination>
        </div>

        <!-- 模型使用步骤 -->
        <div class="card">
            <el-dialog title="计算步骤"
                @close="closeDialog"
                :visible.sync="dialog">
                <div class="step">
                    <el-form >
                        <el-form-item label="步骤1" :label-width="formLabelWidth">
                            <el-button type="primary" v-if="!isKey" @click="getKey">获取密钥</el-button>
                            <el-button type="success" v-else>获取密钥成功</el-button>
                        </el-form-item>
                        <el-form-item label="步骤2" :label-width="formLabelWidth">
                            <el-button type="primary" v-if="!isDataA" @click="getDataAUrl">获取数据A</el-button>
                            <el-button type="success" v-else >获取数据A成功</el-button>
                        </el-form-item>
                        <el-form-item label="步骤3" :label-width="formLabelWidth">
                            <el-button type="primary" v-if="!isDataB" @click="getDataBUrl">获取数据B</el-button>
                            <el-button type="success" v-else >获取数据B成功</el-button>
                        </el-form-item>
                        <el-form-item label="步骤4" :label-width="formLabelWidth">
                            <el-button type="primary" @click="getCompute">计算</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-dialog>
        </div>
        <!-- 就是结果 -->
        <div class="result">
            <el-dialog title="计算结果"
                @close="closeDialog2"
                :visible.sync="dialog2">
                <div class="res">
                    <p>{{ resData["2"] }}</p>
                    <p>{{ resData["3"] }}</p>
                    <p>{{ resData["4"] }}</p>
                    <p>{{ resData["5"] }}</p>
                    <p>{{ resData["8"] }}</p>
                    <p>{{ resData["9"] }}</p>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'

export default {
    name:'User',
    components:{
        Pagination,
    },
    filters:{
        statusType(type){
            const mapType={
                true:'',
                false:'info'
            }
            return mapType[type];
        },
        statusVal(val){
            const mapValue={
                true:'可使用',
                false:'不可使用'
            }
            return mapValue[val];
        }
    },
    data(){
        return{
            formLabelWidth:'120px',
            dialog:false,
            dialog2:false,
            loading:false,
            isDataA:false,
            isDataB:false,
            isKey:false,
            computeList:[],//模型数组
            dataAUrl:null,//数据A地址
            dataBUrl:null,//数据B地址
            dataA:null,
            dataB:null,
            mykey:null,//密钥
            mydata:null,
            result:null,
            delDialogVisible:false,
            compUrl:null,//计算地址
            query:{
                total:null,
                pageSize:5,
                pageIndex:1
            },
            resData:{}
        }
    },
    methods:{
        closeDialog(){
            this.dialog = false;
            this.mykey='';
            this.dataAUrl='';
            this.dataBUrl='';
            this.dataA='';
            this.dataB='';
            this.isKey = false;
            this.isDataA = false;
            this.isDataB = false;
        },
        closeDialog2(){
            this.dialog2=false;
            this.resData = {}
        },
        handleUse(info){
            this.dialog = true;
            let data = info.data.split(/\s/);
            this.dataAUrl = data[0].slice(2,data[0].length-1);
            this.dataBUrl = data[2].slice(2,data[0].length-1);
            this.compUrl = info.url;
        },
        getUserAl(){//获取模型
            let that =this;
            that.loading = true;
            this.$api.useral().then(res=>{
                if(res.err_code === 100){
                    let data = res.data.list;
                    that.computeList = data;
                    that.query.total = data.length; 
                    that.loading = false;
                }                
            })
        },
        getKey(){//获取密钥
            let that = this;
            this.$api.getkey().then(res=>{
                let data = res.data;
                if(res.err_code === 100){
                    that.mykey = data.public_key;
                    that.$message({
                        message:'密钥获取成功',
                        type:'success'
                    })
                    that.isKey = true;
                }
            })
        },
        getDataAUrl(){//申请使用数据a
            let that=this;
            this.$api.usedata(`${this.dataAUrl}`).then(res=>{
                if(res.err_code === 104){
                    that.$message({
                        message: '数据申请使用成功,等待审核'
                    })
                }else if(res.err_code === 105){
                    that.getDataA();
                }
            })
        },
        getDataBUrl(){//申请使用数据b
            let that=this;
            this.$api.usedata(this.dataBUrl).then(res=>{
                if(res.err_code === 104){
                    that.$message({
                        message: '数据申请使用成功,等待审核'
                    })
                }else if(res.err_code === 105){
                    that.getDataB();
                }
            })
        },
        getDataA(){//获取数据A
            let that =this;
            if(!that.mykey){
                return that.$message({
                    message:'请先获取密钥',
                    type:'error'
                })
            }
            that.$api.getdata(`a`,
                `${that.mykey}`).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message: '数据获取成功',
                        type:'success'
                    })
                    that.dataA = res.data.dataA;
                    that.isDataA = true;
                }
            })
        },
        getDataB(){//获取数据B
            let that =this;
            if(!that.mykey){
                return that.$message({
                    message:'请先获取密钥',
                    type:'error'
                })
            }
            that.$api.getdata(`b`,
                `${that.mykey}`).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message: '数据获取成功',
                        type:'success'
                    })
                    that.dataB = res.data.dataB;
                    // that.dataA = res.data.dataB;
                    that.isDataB = true;
                }
            })
        },
        getCompute(){//计算
            let that =this;
            if(!that.dataA){
                return that.$message({
                    message:'请先获取数据A',
                    type:'error'
                })
            }
            if(!that.dataB){
                return that.$message({
                    message:'请先获取数据B',
                    type:'error'
                })
            }
            
            this.$api.comptest(`${this.compUrl}`,{
                a:that.dataA,
                b:that.dataB,
                key: that.mykey
            }).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'计算成功',
                        type:'success'
                    })
                }
                that.closeDialog();
                that.resData= res.data;
                that.dialog2 = true;
            })
        },
        getSize(size){//导航
            this.query.pageSize = size;
            this.getUserAl();
        },
        getIndex(index){
            this.query.pageIndex = index;
            this.getUserAl();
        }
    },
    created(){
        this.getUserAl();
    }
}
</script>

<style lang="scss" scoped>
    .box-title{
        padding: 0 10px;
    }
    .box-container{
        margin: 0 10px 20px;
    }
    .bottom-page{
        background: #fff;
        display: flex;
        justify-content: center;
    }
    .step-btn{
       display: flex;
       justify-content: center;
       margin: 20px;
    }
    .step{
        .step-box{
            display: flex;
            justify-content: flex-start;
        }
    }
    .resData{
        // width: 100px;
        background: #fff;
        padding: 10px;
        margin: 20px;
        overflow-x: scroll;
    }
</style>